module.exports = function init (serviceLocator) {

  serviceLocator.logger.info('Initializing External Image Loader')

  var sourceAttribute = 'data-external-src'
    , $images = $('[' + sourceAttribute + ']')

  if ($images.length === 0) return
  if (!window.navigator.onLine) return

  $images.each(function () {
    var $this = $(this)
      , src = $this.attr(sourceAttribute)
      , style = 'background-image:url(' + src + ')'
      , image = new Image()

    image.onload = function () {
      if ($this.is('[data-external-image]')) {
        $this.attr('src', src).removeAttr('data-external-image')
      }

      if ($this.is('[data-external-background-image]')) {
        $this.attr('style', style).removeAttr('data-external-background-image')
      }

      $this.removeAttr(sourceAttribute)
    }

    image.src = src

  })

}
